    cursor: pointer;
  }
  
  .forms button:hover {
    background-color: #8b8585;
  }
  
  
  
  .afficher:hover {
    background-color: #8b8585;
  }
  input::placeholder{
    color: #22232449;
    font-size: x-small;
  }
  .rech {
    margin: 0 1rem;
